import React from 'react'
import { Router, RouteComponentProps, Link } from '@reach/router'


let Home = (props: RouteComponentProps) => (
  <div>
    <h1>Home</h1>
    <nav>
      <Link to={process.env.PUBLIC_URL + '/'}>Home</Link> |{" "}
      <Link to={process.env.PUBLIC_URL + 'dashboard'}>Dashboard</Link>
    </nav>
  </div>
)

let Dash = (props: RouteComponentProps) => <div>Dash</div>

function App() {
  return (
    <div className="App">
      <Router>
        <Home path={process.env.PUBLIC_URL + '/'} />
        <Dash path={process.env.PUBLIC_URL + 'dashboard'} />
      </Router>
    </div>
  );
}

export default App;
